import trendsList from '@/components/list/trends-list'
import youScroll from '@/components/you-scroll/you-scroll.vue'
//======================================================================
import _find_data from '@/static/zaiui/data/find.js' //虚拟数据
import handles from '@/utils/handles.js'
import {mapState} from 'vuex'

const iniNotice = ()=> ({
	pageIndex:0,
	pageSize:20,
	startTime:new Date().format('yyyy-MM-dd hh:mm'),
	finish: false,
	list:[]
})
const noticeMap = tabCur => {
	switch(tabCur){
		case 0:
			return 'notices'
		case 1:
			return 'sellNotices'
		case 2:
			return 'demandNotices'
		case 3:
			return 'myNotices'
		default:
			console.log('case default');
	}
}

export default {
	name: 'find',
	components: {
		trendsList,
		youScroll
	},
	data() {
		return {
			tabCur: 0,
			tabData: ['广场', '出售', '需求','我发布的'],
			notices: {list:[]},
			sellNotices:{list:[]},
			demandNotices:{list:[]},
			myNotices:{list:[]},
			loadStatus: 'more'
		}
	},
	computed:{
		...mapState(['userId'])
	},
	props: {
		show: {
			type: Boolean,
			default: true
		},
		scrollBottom: {
			type: Boolean,
			default: false
		}
	},
	watch: {
		scrollBottom(newVal) {
			if (newVal) {
				this.setReachBottom()
			}
		}
	},
	created() {
		this.notices = iniNotice()
		//加载虚拟数据
		this.notices.list = _find_data.trendsData()		
		// 请求
		
	},
	mounted() {
		uni.pageScrollTo({
			scrollTop: 0,
			duration: 101
		})
	},
	methods: {
		async loadNotices(){
			this.loadStatus = 'loading'
			let curNotices = this[noticeMap(this.tabCur)]
			let pagination = {
				pageIndex:curNotices.pageIndex,
				pageSize: curNotices.pageSize,
				startTime:curNotices.startTime
			}
			if(this.tabCur > 2){
				await this.$api.getMyNotices(this.userId,pagination)
					.then(({data})=>{
						curNotices.list.push(...data.list)
						// 取完了数据
						if(data.pageIndex === data.pageCount)
							curNotices.finish = true
					})
					.catch(()=>uni.showToast({
						title: '我的通告获取失败',
						icon: 'none'
					}))
			}
			else
				await this.$api.getNotices(this.tabCur, pagination)
					.then(({data})=> {
						// 一些没有判断 success,根据后台的 json 来决定要不要加这个判断
						curNotices.list.push(...data.list)
						// 取完了数据
						if(data.pageIndex === data.pageCount)
							curNotices.finish = true
					})
					.catch(() => uni.showToast({
						title: '通告获取失败',
						icon: 'none'
					}))
			
			console.log('运行到此，设置 loadingText');
			if(curNotices.finish)
				this.loadStatus = 'noMore'
			else
				this.loadStatus = 'more'
		},
		async onPullDown(done){
			this[noticeMap(this.tabCur)] = iniNotice()
			await this.loadNotices()
			done()		
		},
		setReachBottom() {
			console.log('notice 触底了')
			if(!this[noticeMap(this.tabCur)].finish)
				this.loadNotices()
		},
		tabSelect(e) {
			let current = parseInt(e.currentTarget.dataset.id)		
			if(this.tabCur !== current && this[noticeMap(current)].list.length === 0){
				this[noticeMap(current)] = iniNotice()
				this.tabCur = current
				this.loadNotices()
			}else
				this.tabCur = current
		},
		userTap({data}) {
			console.log('用户区域被点击：',data)
			
			// uni.navigateTo({
			// 	animationType:'auto',
			// 	url:'../../pages/account/'
			// })
		},
		titleTap({data}) {
			console.log('标题区域被点击：',data)
			uni.navigateTo({
				url: '../../pages/notice-detail/notice-detail?id='+data.id
				
			})
		},
		contentTap({data}) {
			console.log('文字内容被点击：',data)
			uni.navigateTo({
				url: '../../pages/notice-detail/notice-detail?id='+data.id
				
			})
			// uni.navigateTo({
			// 	animationType:'auto',
			// 	url:'../../pages/notices/detail'
			// })
		},
		imgTap(e) {
			
			console.log('图片被点击：' + JSON.stringify(e))
		},
		viewAllTap() {
			console.log('点击了查看全部')
		}
	}
}
